html {
  font-size: 12px;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Bebas Neue', 'Roboto', 'Segoe UI', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* hard-coding image url to avoid creating proxy server to protect unsplash API keys */
/* overlay blend to reduce image saturation */

#root {
  background-image: url('https://images.unsplash.com/photo-1533719456375-e025fc0caffd'),
    linear-gradient(0deg, rgb(0 0 0 /0.5), rgb(0 0 0 / 0.2));
  background-blend-mode: overlay;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
